html,body{margin:0;padding:0;font-size:100%;font-family:var(--app-font-family);-webkit-font-smoothing:subpixel-antialiased}body *{font-family:var(--app-font-family);-webkit-font-smoothing:subpixel-antialiased}body>iframe{display:none}h1,h2,h3{margin:0}#root{padding:0;margin:0}.app-page .app-layout-root{min-height:100vh;width:100%;max-width:100%;display:flex;flex-flow:column;transition:filter 0.3s ease-in-out, max-width, 0.1s ease-in-out}.app-page .app-layout-root .page-wrapper{display:flex;flex-flow:column;flex:1 0 auto;height:inherit;margin-top:0em;display:flex;flex-flow:column;transition-property:margin-top;transition-timing-function:ease-in-out;transition-duration:0.3s}.app-page .app-layout-root .page-wrapper .page-body{flex:1 0 auto;background:#f8f8f8}.app-page .app-layout-root .page-wrapper.not-found-page-wrapper_{flex:1 0 auto;background:#f4f4f4}.app-page .app-layout-root .page-wrapper.not-found-page-wrapper_ .app-action-wrapper{display:flex;flex-flow:column;justify-content:center;flex:1 0 100%}.app-page .app-layout-root .page-wrapper.not-found-page-wrapper_ .app-action-wrapper .app-action-footer-buttons{display:grid;width:100%;grid-template-columns:repeat(auto-fit, minmax(10ch, auto));grid-gap:0.4em 1em;align-items:center;justify-content:center}.app-page .app-layout-root .app-error-root{position:absolute;top:0;left:0;bottom:0;width:100%;background:rgba(255,255,255,0.9);display:flex;flex-flow:column;align-items:center;justify-content:center;z-index:4;transition-property:top;transition-duration:0.3s;transition-timing-function:ease-out}.app-page .app-layout-root .app-error-root .app-error-title{flex:0 0 auto;font-size:2.6em;font-weight:500;color:#d9252b}.app-page .app-layout-root .app-error-root .app-error-body{flex:1 1 auto;margin-top:1em;display:flex;flex-flow:column;align-items:center;justify-content:center}.app-page .app-layout-root .app-error-root .app-error-body .app-error-message{margin-top:1.5em}.app-page .app-layout-root .app-error-root .app-error-footer{flex:0 0 auto;margin:0.4em 0;display:flex;align-items:center;justify-content:center}.app-page.has-app-header .app-error-root{margin-top:4em}.app-page.has-app-header.has-app-header-toolbar .app-error-root{margin-top:7em;height:calc(100vh - 7em)}.app-page.has-app-header.has-app-error-fatal .app-error-root{margin-top:0em;height:100vh}.app-page.app-overlay-visible .app-layout-root{filter:saturate(0.3) blur(2px)}.app-page .suspense-overlay-loader,.app-page .app-loader-root{z-index:5}.app-page .suspense-overlay-loader.redirect-loader-root,.app-page .app-loader-root.redirect-loader-root{background:#fff;text-align:center}.app-page.app-loading-user .suspense-overlay-loader,.app-page.app-loading-user .app-loader-root{height:calc(100vh - 4em);margin-top:4em;background:rgba(255,255,255,0.96)}.app-page.has-app-header .suspense-overlay-loader,.app-page.has-app-header .app-loader-root{height:calc(100vh - 4em);margin-top:4em}.app-page.has-app-debug .app-layout-root{max-width:80vw}.app-page.has-app-debug .app-error-root,.app-page.has-app-debug .app-loader-root,.app-page.has-app-debug .suspense-overlay-loader{width:80vw}.app-page .app-layout-teacher-login .session-loader-root{display:none}.app-page.terms-not-accepted .app-layout-root .page-wrapper .page-root{pointer-events:none}.app-page.has-app-error-fatal .app-error-root{margin-top:0;height:100%;z-index:9}
