.elevate-table.elevate-table-loading .elevate-tr {
            opacity: 0.8;
            pointer-events: none;
            filter: blur(2px);
        }
.elevate-table.elevate-table-loading .MuiTableHead-root {
            opacity: 1;
            pointer-events: none;
