.app-page .modal-dialog-wrapper {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: 10;
        opacity: 0;

        transition-property: opacity, right;
        transition-duration: 0.3s;
        transition-timing-function: ease-in-out

    }

.app-page .modal-dialog-wrapper .modal-dialog-mask {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background: rgba(255,255,255,0.5);
            display: flex;
            flex-flow: column;
            align-items: center;
            justify-content: center
        }

.app-page .modal-dialog-wrapper .modal-dialog-mask.closable-modal-dialog-mask {
                cursor: pointer;
            }

.app-page .modal-dialog-wrapper .close-modal-button {
            cursor: pointer;
        }

.app-page .modal-dialog-wrapper .modal-dialog-contents {
            cursor: initial;
            background: #FFFFFF;
            border: 1px solid #D7DAE2;
            border-radius: 0.375em;
            padding: 0.625em;
            min-width: 30vw;
            max-height: 90vh;

            transition-property: margin-top, max-height;
            transition-duration: 0.3s;
            transition-timing-function: ease-in-out;
        }

.app-page .modal-dialog-wrapper .modal-dialog-top {
            display: flex;
            align-items: center;
            justify-content: space-between
        }

.app-page .modal-dialog-wrapper .modal-dialog-top .icon-link {
                font-size: 2em;
            }

.app-page .modal-dialog-wrapper .modal-dialog-title {
            font-size: 1.5em;
            font-weight: bold;
            padding: 0 0.3125em;
        }

.app-page .modal-dialog-wrapper .modal-dialog-body {
            padding: 0.3125em;
            max-height: 80vh;
            overflow: auto;
        }

.app-page .modal-dialog-wrapper.modal-dialog-initialized {
            opacity: 1;
        }

.app-page.has-app-notification .modal-dialog-wrapper .modal-dialog-contents {
                margin-top: 4em;
                max-height: calc(90vh - 4em);
            }
.app-loader-root {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    background: rgba(255, 255, 255, 0.9);
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.app-loader-title {
    font-size: 2.6em;
    font-weight: 500;
}

.app-loader-body {
    margin-top: 1em;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.app-loader-message,
.app-loader-actions {
    margin-top: 1.5em;
}

.overlay-loader-root {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    background: rgba(255, 255, 255, 0.9);
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.overlay-loader-title {
    font-size: 2.6em;
    font-weight: 500;
}

.overlay-loader-body {
    margin-top: 1em;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.overlay-loader-message,
.overlay-loader-actions {
    margin-top: 1.5em;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.loader-spinner {
    animation: spin 2s linear infinite;
}

.overlay-error-root {
    position: absolute;
