      height: 2px;
      margin: 0 auto;
      background-color: #d8e1e9;
      position: relative;
      /*top: 30px;*/
      transition: all 0.15s ease-in-out
    }


#main-navigation-hamburger .ham:before,
      #main-navigation-hamburger .ham:after {
        content: "";
        position: absolute;
        height: 2px;
        left: 0;
        background-color: #d8e1e9;
        transition: all 0.15s ease-in-out;
      }


#main-navigation-hamburger .ham:before {
        top: -5px;
        width: 70%;
      }


#main-navigation-hamburger .ham:after {
        top: 5px;
        width: 85%;
      }


#main-navigation-hamburger:hover .ham {
        background-color: #fff
      }


#main-navigation-hamburger:hover .ham:before,
        #main-navigation-hamburger:hover .ham:after {
          width: 100%;
          background-color: #fff;
        }
