.elevate-header-sensitive-tooltip {
    position: relative;
    display: inline-block
  }









.elevate-header-sensitive-tooltip .ehst-tooltip-text {
        font-size: 1em;
        color: #fff;
        background-color: #000;
        position: absolute;
        padding: 0.21875rem 0.5rem 0.3125rem 0.5rem;
        white-space: nowrap;
        border-radius: 0.5625em;
        transition-property: opacity, transform;
        transition-duration: 0.3s;
        transition-delay: 0.6s;
        transition-timing-function: ease-in-out;
        z-index: 2;
        pointer-events: none;
        opacity: 0;
        left: 0;
        top: 50%;
        transform: translate(-80%, -50%);
        text-align: left
    }









.elevate-header-sensitive-tooltip .ehst-tooltip-text:after {
            content: "";
            position: absolute;
            display: inline-block;
            width: 0;
            height: 0;
            border: 0.25em solid transparent;
            border-left-color: #000;
            top: 50%;
            transform: translateY(-50%);
            left: 100%;
            overflow: visible;
        }









.elevate-header-sensitive-tooltip:hover .ehst-tooltip-text {
            pointer-events: all;
            opacity: 1;
            transition-delay: 0s;
            transform: translate(-110%, -50%);
        }
  
